.thank-section {
	position: relative;
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 360px;
		transform: translate(-70%, -15%);
		z-index: 3;
		@include min-md {
			width: 380px;
			transform: translate(-60%, -15%);
		}
		@include min-lg {
			width: 420px;
		}
		.orn-2 {
			position: absolute;
			top: 30%;
			right: 10%;
			width: 80px;
			transform: translate(-20%, 0);
			@include min-md {
				width: 100px;
			}
			@include min-lg {
				width: 120px;
			}
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		right: 0;
		width: 460px;
		transform: translate(60%, -20%);
		z-index: 3;
		@include min-md {
			width: 520px;
		}
		@include min-lg {
			width: 560px;
		}
		.orn-4 {
			position: absolute;
			top: 23%;
			left: 25%;
			width: 100px;
			transform: scaleX(-1);
			@include min-md {
				width: 120px;
			}
			@include min-lg {
				width: 140px;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 200px;
		transform: translate(-50%, 10%);
		z-index: 3;
		@include min-md {
			width: 220px;
		}
		@include min-lg {
			width: 240px;
		}
		.orn-6 {
			position: absolute;
			top: 50%;
			right: 0;
			width: 180px;
			transform: translate(30%, 0);
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				width: 240px;
				transform: translate(50%, 0);
			}
		}
		.orn-7 {
			position: absolute;
			top: 0;
			left: 50%;
			width: 80px;
			transform: translate(-30%, 30%);
			@include min-md {
				width: 100px;
			}
			@include min-lg {
				width: 120px;
			}
		}
		.orn-8 {
			position: absolute;
			top: 60%;
			left: 50%;
			width: 160px;
			transform: translate(-30%, 10%);
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				width: 200px;
			}
		}
		.orn-9 {
			position: absolute;
			top: 35%;
			left: 50%;
			width: 80px;
			transform: translate(-40%, 25%);
			@include min-md {
				width: 100px;
			}
			@include min-lg {
				width: 120px;
			}
		}
		.orn-10 {
			position: absolute;
			top: 60%;
			right: 0;
			width: 100px;
			transform: translate(50%, 10%);
			@include min-md {
				width: 120px;
			}
			@include min-lg {
				width: 140px;
			}
		}
		.orn-17 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 80px;
			transform: translate(50%, 0%);
			@include min-md {
				width: 100px;
			}
			@include min-lg {
				width: 120px;
			}
		}
	}
	.orn-11 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 200px;
		transform: translate(58%, 20%) rotate(-10deg);
		z-index: 3;
		@include min-md {
			width: 220px;
			transform: translate(48%, 0) rotate(-10deg);
		}
		@include min-lg {
			width: 240px;
		}
		.orn-12 {
			position: absolute;
			top: 35%;
			left: 0;
			width: 160px;
			transform: translate(-20%, 0) scaleX(-1);
			@include min-md {
				top: unset;
				bottom: 0;
				width: 200px;
				transform: translate(-30%, 30%) scaleX(-1);
			}
			@include min-lg {
				width: 240px;
			}
		}
		.orn-13 {
			position: absolute;
			top: 20%;
			left: 20%;
			width: 80px;
			transform: scaleX(-1);
			@include min-md {
				top: unset;
				bottom: 0;
				width: 100px;
			}
			@include min-lg {
				width: 120px;
			}
		}
		.orn-14 {
			position: absolute;
			top: 50%;
			left: 0;
			width: 140px;
			transform: translate(-70%, 10%) rotate(10deg) scaleX(-1);
			@include min-md {
				width: 160px;
				transform: translate(-75%, 35%) rotate(10deg) scaleX(-1);
			}
			@include min-lg {
				width: 180px;
			}
		}
		.orn-15 {
			position: absolute;
			top: 40%;
			left: 10%;
			width: 100px;
			transform: translate(0, 5%) scaleX(-1);
			@include min-md {
				top: 65%;
				width: 120px;
				transform: translate(-30%, -20%) scaleX(-1);
			}
			@include min-lg {
				width: 140px;
			}
		}
		.orn-16 {
			position: absolute;
			top: 50%;
			left: 0;
			width: 80px;
			transform: translate(-50%, 0) scaleX(-1);
			@include min-md {
				top: 75%;
				width: 100px;
				transform: translate(-80%, 0) scaleX(-1);
			}
			@include min-lg {
				width: 120px;
			}
		}
		.orn-18 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 80px;
			transform: translate(-60%, -20%) scaleX(-1);
			@include min-md {
				width: 100px;
				transform: translate(-60%, 0%) scaleX(-1);
			}
			@include min-lg {
				width: 120px;
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 1;
		margin-bottom: 2rem;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 300px;
			}
			.frame {
				position: absolute;
				top: 50%;
				left: 50%;
				width: 125%;
				transform: translate(-50%, -50%) rotateZ(25deg);
			}
		}
	}
	.thank-wrapper {
		position: relative;
		overflow: hidden;
		padding: 5rem 0;
		text-align: center;
		@include max-md {
			padding: 8rem 0;
		}
		.elements-wrapper-absolute {
			.background-element {
				overflow: hidden;
				border-radius: 300px 300px 20px 20px;
				box-shadow: inset 0 20px 40px rgba($color: #000000, $alpha: 0.2);
				img {
					opacity: 0.4;
				}
			}
		}
		.thank {
			position: relative;
			.thank-body {
				padding: 3rem 2rem 5rem;
				@include max-md {
					padding: 2rem 1rem 5rem;
				}
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
