.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	width: 100%;
	&.cover-opened {
		transform: translateY(-100%);
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 360px;
		transform: translate(-50%, -40%);
		@include min-md {
			width: 380px;
		}
		@include min-lg {
			width: 420px;
		}
		.orn-2 {
			position: absolute;
			top: 50%;
			right: 30%;
			width: 80px;
			transform: translate(-20%, 0);
			@include min-md {
				width: 100px;
			}
			@include min-lg {
				width: 120px;
			}
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		right: 0;
		width: 420px;
		transform: translate(60%, -30%);
		@include min-md {
			width: 440px;
		}
		@include min-lg {
			width: 480px;
		}
		.orn-4 {
			position: absolute;
			top: 50%;
			left: 25%;
			width: 100px;
			transform: scaleX(-1);
			@include min-md {
				width: 120px;
			}
			@include min-lg {
				width: 140px;
			}
		}
		.orn-5 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 200px;
			transform: translate(30%, 20%) scaleX(-1);
			@include min-md {
				width: 240px;
			}
			@include min-lg {
				width: 260px;
			}
		}
	}
	.orn-6 {
		position: absolute;
		top: 50%;
		left: 0;
		width: 200px;
		transform: translate(-40%, -50%);
		@include min-md {
			width: 220px;
		}
		@include min-lg {
			width: 240px;
		}
		.orn-7 {
			position: absolute;
			top: 0;
			right: 10%;
			width: 100px;
			transform: translate(-30%, -50%) rotate(15deg);
			@include min-md {
				width: 120px;
			}
			@include min-lg {
				width: 140px;
			}
		}
	}
	.orn-8 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 460px;
		transform: translate(-55%, 30%) scaleX(-1);
		@include min-md {
			width: 480px;
		}
		@include min-lg {
			width: 520px;
		}
		.orn-9 {
			position: absolute;
			top: 30%;
			left: 0;
			width: 200px;
			transform: translate(40%, 0) scaleX(-1);
			@include min-md {
				width: 220px;
			}
			@include min-lg {
				width: 240px;
			}
		}
		.orn-10 {
			position: absolute;
			top: 50%;
			left: 30%;
			width: 100px;
			transform: scaleX(-1);
			@include min-md {
				width: 120px;
			}
			@include min-lg {
				width: 140px;
			}
		}
	}
	.orn-11 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 380px;
		transform: translate(70%, 10%) scaleX(-1);
		@include min-md {
			width: 420px;
		}
		@include min-lg {
			width: 440px;
		}
		.orn-12 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 180px;
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				width: 240px;
			}
		}
		.orn-13 {
			position: absolute;
			top: 50%;
			right: 10%;
			width: 100px;
			transform: translate(-15%, -40%) rotate(10deg);
			@include min-md {
				width: 120px;
			}
			@include min-lg {
				width: 140px;
			}
		}
		.orn-14 {
			position: absolute;
			top: 0;
			right: 10%;
			width: 100px;
			transform: translate(-20%, -20%);
			@include min-md {
				width: 120px;
			}
			@include min-lg {
				width: 140px;
			}
		}
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		height: 100vh;
		@include max-lg {
			padding: 4rem 0 6rem;
			align-items: center;
		}
		.cover-content {
			text-align: center;
			position: relative;
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			.cover-body {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				padding: 3rem 2rem;
				border-radius: 300px 300px 20px 20px;
				width: 40%;
				min-height: 80%;
				@include max-lg {
					width: 60%;
				}
				@include max-md {
					width: 100%;
				}
				&::before {
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					background-color: var(--color-primary);
					border-radius: 300px 300px 20px 20px;
					opacity: 0.6;
					content: "";
					box-shadow: inset 0 1.5rem 2.5rem rgba($color: #000000, $alpha: 0.2);
				}
				.greeting-wrapper {
					position: relative;
					@include max-md {
						font-size: 14px;
					}
					h6 {
						font-weight: 700;
					}
				}
				.cover-description {
					position: relative;
					padding: 0 2rem;
					text-align: center;
					color: var(--color-dark);
					@include max-lg {
						padding: 2rem 0 1rem;
					}
					* {
						position: relative;
					}
					.title {
						padding: 1rem 0;
					}
					.cover-event {
						position: relative;
						h6,
						span {
							font-weight: 600;
							margin-bottom: 0;
						}
					}
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
