.story-section {
	position: relative;
	.orn-1 {
		position: absolute;
		top: 5%;
		left: 0;
		width: 180px;
		transform: translate(-40%, 0);
		z-index: 3;
		@include min-md {
			top: unset;
			bottom: 5rem;
			width: 200px;
		}
		@include min-lg {
			width: 220px;
		}
		.orn-2 {
			position: absolute;
			top: 0;
			right: 0;
			width: 100px;
			transform: translate(-70%, 0);
			@include min-md {
				width: 120px;
				transform: translate(-60%, 0);
			}
			@include min-lg {
				width: 140px;
			}
		}
	}
	.orn-3 {
		position: absolute;
		top: 5%;
		right: 0;
		width: 200px;
		transform: translate(50%, 0) scaleX(-1);
		z-index: 3;
		@include min-md {
			top: unset;
			bottom: 3rem;
			width: 200px;
		}
		@include min-lg {
			width: 220px;
		}
		.orn-4 {
			position: absolute;
			top: 20%;
			right: 0;
			width: 100px;
			transform: translate(-30%, 0);
			@include min-md {
				top: 40%;
				width: 120px;
			}
			@include min-lg {
				width: 140px;
			}
		}
	}
	.story {
		position: relative;
		padding: 5rem 0 0;
		margin-bottom: -5rem;
		z-index: 5;
		@include max-md {
			padding: 3rem 0 0;
			margin-bottom: -3rem;
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		text-align: center;
		padding: 7rem 2rem 2rem;
		background-color: var(--color-primary);
		border-radius: 300px 300px 20px 20px;
		@include max-md {
			padding: 7rem 1rem 1rem;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		@include max-md {
			aspect-ratio: 6/5;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
			}
		}
	}
}
