.header-section {
	position: relative;
	width: 100%;
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 360px;
		transform: translate(-70%, -15%);
		z-index: 3;
		@include min-md {
			width: 380px;
			transform: translate(-60%, -15%);
		}
		@include min-lg {
			width: 420px;
		}
		.orn-2 {
			position: absolute;
			top: 30%;
			right: 10%;
			width: 80px;
			transform: translate(-20%, 0);
			@include min-md {
				width: 100px;
			}
			@include min-lg {
				width: 120px;
			}
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		right: 0;
		width: 460px;
		transform: translate(60%, -20%);
		z-index: 3;
		@include min-md {
			width: 520px;
		}
		@include min-lg {
			width: 560px;
		}
		.orn-4 {
			position: absolute;
			top: 23%;
			left: 25%;
			width: 100px;
			transform: scaleX(-1);
			@include min-md {
				width: 120px;
			}
			@include min-lg {
				width: 140px;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 200px;
		transform: translate(-50%, 10%);
		z-index: 3;
		@include min-md {
			width: 220px;
		}
		@include min-lg {
			width: 240px;
		}
		.orn-6 {
			position: absolute;
			top: 50%;
			right: 0;
			width: 180px;
			transform: translate(30%, 0);
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				width: 240px;
				transform: translate(50%, 0);
			}
		}
		.orn-7 {
			position: absolute;
			top: 0;
			left: 50%;
			width: 80px;
			transform: translate(-30%, 30%);
			@include min-md {
				width: 100px;
			}
			@include min-lg {
				width: 120px;
			}
		}
		.orn-8 {
			position: absolute;
			top: 60%;
			left: 50%;
			width: 160px;
			transform: translate(-30%, 10%);
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				width: 200px;
			}
		}
		.orn-9 {
			position: absolute;
			top: 35%;
			left: 50%;
			width: 80px;
			transform: translate(-40%, 25%);
			@include min-md {
				width: 100px;
			}
			@include min-lg {
				width: 120px;
			}
		}
		.orn-10 {
			position: absolute;
			top: 60%;
			right: 0;
			width: 100px;
			transform: translate(50%, 10%);
			@include min-md {
				width: 120px;
			}
			@include min-lg {
				width: 140px;
			}
		}
		.orn-17 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 80px;
			transform: translate(50%, 0%);
			@include min-md {
				width: 100px;
			}
			@include min-lg {
				width: 120px;
			}
		}
	}
	.orn-11 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 200px;
		transform: translate(58%, 20%) rotate(-10deg);
		z-index: 3;
		@include min-md {
			width: 220px;
			transform: translate(48%, 0) rotate(-10deg);
		}
		@include min-lg {
			width: 240px;
		}
		.orn-12 {
			position: absolute;
			top: 35%;
			left: 0;
			width: 160px;
			transform: translate(-20%, 0) scaleX(-1);
			@include min-md {
				top: unset;
				bottom: 0;
				width: 200px;
				transform: translate(-30%, 30%) scaleX(-1);
			}
			@include min-lg {
				width: 240px;
			}
		}
		.orn-13 {
			position: absolute;
			top: 20%;
			left: 20%;
			width: 80px;
			transform: scaleX(-1);
			@include min-md {
				top: unset;
				bottom: 0;
				width: 100px;
			}
			@include min-lg {
				width: 120px;
			}
		}
		.orn-14 {
			position: absolute;
			top: 50%;
			left: 0;
			width: 140px;
			transform: translate(-70%, 10%) rotate(10deg) scaleX(-1);
			@include min-md {
				width: 160px;
				transform: translate(-75%, 35%) rotate(10deg) scaleX(-1);
			}
			@include min-lg {
				width: 180px;
			}
		}
		.orn-15 {
			position: absolute;
			top: 40%;
			left: 10%;
			width: 100px;
			transform: translate(0, 5%) scaleX(-1);
			@include min-md {
				top: 65%;
				width: 120px;
				transform: translate(-30%, -20%) scaleX(-1);
			}
			@include min-lg {
				width: 140px;
			}
		}
		.orn-16 {
			position: absolute;
			top: 50%;
			left: 0;
			width: 80px;
			transform: translate(-50%, 0) scaleX(-1);
			@include min-md {
				top: 75%;
				width: 100px;
				transform: translate(-80%, 0) scaleX(-1);
			}
			@include min-lg {
				width: 120px;
			}
		}
		.orn-18 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 80px;
			transform: translate(-60%, -20%) scaleX(-1);
			@include min-md {
				width: 100px;
				transform: translate(-60%, 0%) scaleX(-1);
			}
			@include min-lg {
				width: 120px;
			}
		}
	}
	.header-content {
		position: relative;
		height: 100vh;
		padding: 4.5rem 0 0;
		display: flex;
		justify-content: center;
		.header {
			position: relative;
			width: 40%;
			height: 100%;
			padding: 5rem 1.5rem 3rem;
			@include max-xl {
				width: 50%;
			}
			@include max-lg {
				width: 60%;
			}
			@include max-md {
				width: 100%;
			}
			.elements-wrapper-absolute {
				overflow: hidden;
				border-radius: 300px 300px 0 0;
				box-shadow: inset 0 20px 40px rgba($color: #000000, $alpha: 0.2);
			}
			.header-title {
				position: relative;
				text-align: center;
				&-content {
					position: relative;
					border-radius: var(--border-radius);
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
			.image-wrapper {
				position: relative;
				width: 80%;
				height: auto;
				aspect-ratio: 4/6;
				margin: 0 auto;
				align-self: flex-end;
				.header-background {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
				.image-element {
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					width: 90%;
					height: 93%;
					.header-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
