.stream-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 30%;
		left: 0;
		width: 360px;
		transform: translate(-70%, -40%);
		@include min-md {
			width: 380px;
		}
		@include min-lg {
			width: 420px;
		}
		.orn-2 {
			position: absolute;
			top: 30%;
			right: 10%;
			width: 80px;
			transform: translate(-20%, 0);
			@include min-md {
				width: 100px;
			}
			@include min-lg {
				width: 120px;
			}
		}
	}
	.orn-3 {
		position: absolute;
		top: 30%;
		right: 0;
		width: 420px;
		transform: translate(60%, -30%);
		@include min-md {
			width: 440px;
		}
		@include min-lg {
			width: 460px;
		}
		.orn-4 {
			position: absolute;
			top: 50%;
			left: 25%;
			width: 100px;
			transform: scaleX(-1);
			@include min-md {
				width: 120px;
			}
			@include min-lg {
				width: 140px;
			}
		}
		.orn-5 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 200px;
			transform: translate(30%, 20%) scaleX(-1);
			@include min-md {
				width: 220px;
			}
			@include min-lg {
				width: 240px;
			}
		}
	}
	.orn-6 {
		position: absolute;
		top: 50%;
		left: 0;
		width: 200px;
		transform: translate(-40%, -50%);
		@include min-md {
			width: 220px;
		}
		@include min-lg {
			width: 240px;
		}
		.orn-7 {
			position: absolute;
			top: 0;
			right: 10%;
			width: 100px;
			transform: translate(-30%, -50%) rotate(15deg);
			@include min-md {
				width: 120px;
			}
			@include min-lg {
				width: 140px;
			}
		}
	}
	.orn-8 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 460px;
		transform: translate(-55%, 30%) scaleX(-1);
		@include min-md {
			width: 480px;
		}
		@include min-lg {
			width: 520px;
		}
		.orn-9 {
			position: absolute;
			top: 30%;
			left: 0;
			width: 200px;
			transform: translate(40%, 0) scaleX(-1);
			@include min-md {
				width: 220px;
			}
			@include min-lg {
				width: 240px;
			}
		}
		.orn-10 {
			position: absolute;
			top: 50%;
			left: 30%;
			width: 100px;
			transform: scaleX(-1);
			@include min-md {
				width: 120px;
			}
			@include min-lg {
				width: 140px;
			}
		}
	}
	.orn-11 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 380px;
		transform: translate(70%, 10%) scaleX(-1);
		@include min-md {
			width: 420px;
		}
		@include min-lg {
			width: 440px;
		}
		.orn-12 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 180px;
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				width: 220px;
			}
		}
		.orn-13 {
			position: absolute;
			top: 50%;
			right: 10%;
			width: 100px;
			transform: translate(-15%, -40%) rotate(10deg);
			@include min-md {
				width: 120px;
			}
			@include min-lg {
				width: 140px;
			}
		}
		.orn-14 {
			position: absolute;
			top: 0;
			right: 10%;
			width: 100px;
			transform: translate(-20%, -20%);
			@include min-md {
				width: 120px;
			}
			@include min-lg {
				width: 140px;
			}
		}
	}
	.orn-15 {
		position: absolute;
		top: 0;
		left: 0;
		width: 360px;
		transform: translate(-73%, -20%);
		z-index: 3;
		@include min-md {
			width: 380px;
		}
		@include min-lg {
			width: 420px;
		}
		.orn-16 {
			position: absolute;
			top: 20%;
			right: 10%;
			width: 80px;
			transform: translate(-20%, 0);
			@include min-md {
				width: 100px;
			}
			@include min-lg {
				width: 120px;
			}
		}
	}
	.orn-17 {
		position: absolute;
		top: 0;
		right: 0;
		width: 460px;
		transform: translate(65%, -30%);
		z-index: 3;
		@include min-md {
			width: 480px;
		}
		@include min-lg {
			width: 520px;
		}
		.orn-18 {
			position: absolute;
			top: 25%;
			left: 25%;
			width: 100px;
			transform: scaleX(-1);
			@include min-md {
				width: 120px;
			}
			@include min-lg {
				width: 140px;
			}
		}
	}
	.stream-wrapper {
		position: relative;
		.stream-content {
			position: relative;
			.stream-description {
				position: relative;
				padding: 3rem 2rem;
				@include max-md {
					padding: 2rem 1rem;
				}
				&::before {
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					background-color: var(--color-primary);
					border-top-left-radius: var(--border-radius);
					border-top-right-radius: var(--border-radius);
					opacity: 0.5;
					content: "";
				}
			}
			h4 {
				@include max-md {
					text-align: center;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		position: relative;
		width: 100%;
	}
}
