.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 320px;
		transform: translate(-40%, 30%);
		@include min-md {
			width: 340px;
		}
		@include min-lg {
			width: 360px;
		}
		.orn-2 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 180px;
			transform: translate(-10%, 0);
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				width: 220px;
			}
		}
		.orn-3 {
			position: absolute;
			bottom: 0;
			left: 50%;
			width: 100px;
			transform: translate(-50%, -10%);
			@include min-md {
				width: 120px;
			}
			@include min-lg {
				width: 140px;
			}
		}
		.orn-4 {
			position: absolute;
			top: 0;
			left: 50%;
			width: 120px;
			transform: translate(-50%, 50%);
			@include min-md {
				width: 140px;
			}
			@include min-lg {
				width: 160px;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 460px;
		transform: translate(50%, 38%);
		@include min-md {
			width: 480px;
		}
		@include min-lg {
			width: 520px;
		}
		.orn-6 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 180px;
			transform: translate(40%, -20%);
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				width: 220px;
			}
		}
		.orn-7 {
			position: absolute;
			left: 10%;
			top: 55%;
			width: 120px;
			transform: translate(50%, -50%) scaleX(-1);
			@include min-md {
				width: 140px;
			}
			@include min-lg {
				width: 160px;
			}
		}
	}
	.rsvp-wrapper {
		position: relative;
		padding: 3rem 2rem;
		@include max-md {
			padding: 2rem 1rem;
		}
		.rsvp-form {
			position: relative;
			.title-section {
				&::before {
					all: unset;
				}
			}
			.form-control,
			.form-select {
				background-color: rgba($color: #fff, $alpha: 0.7);
			}
		}
	}
}
